.FlexEjemplo{
    display: flex;
    padding: 5px;
    border: 1px solid var(--colorSombra);
    height: 150px;
    background: var(--colorAzulado);
}

.Justify-content--flex-start{
    justify-content: flex-start;
}

.Justify-content--flex-end{
    justify-content: flex-end;
}

.Justify-content--center{
    justify-content: center;
}
.Justify-content--space-between{
    justify-content: space-between;
}
.Justify-content--space-around{
    justify-content: space-around;
}
.Justify-content--space-evenly{
    justify-content: space-evenly;
}
.Flex-direction--row{
    flex-direction: row;
}
.Flex-direction--column{
    flex-direction: column;
}
.Flex-direction--row-reverse{
    flex-direction: row-reverse;
}
.Flex-direction--column-reverse{
    flex-direction: column-reverse;
}


.Flex-wrap--wrap,.Flex-wrap--wrap-reverse{
    height: auto;
}
.Flex-wrap--wrap .FlexElemento,.Flex-wrap--wrap-reverse .FlexElemento,.Flex-wrap--nowrap .FlexElemento{
    width: 40%;
}
.Flex-wrap--nowrap{
    flex-wrap: nowrap;
}
.Flex-wrap--wrap{
    flex-wrap: wrap;
}
.Flex-wrap--wrap-reverse{
    flex-wrap: wrap-reverse;
}


.Aling-items--flex-start .FlexElemento,
.Aling-items--flex-end .FlexElemento,
.Aling-items--center .FlexElemento,
.Aling-items--stretch .FlexElemento{
    width: 40%;
}
.Aling-items--flex-start,.Aling-items--flex-end,.Aling-items--center,.Aling-items--stretch{
    flex-wrap: wrap;
}
.Aling-items--flex-start{
    align-items: flex-start;
}
.Aling-items--center{
    align-items: center;
}
.Aling-items--flex-end{
    align-items: flex-end;
}
.Aling-items--stretch > .FlexElemento{
    height: initial;
}
.Aling-items--stretch{
    align-items: stretch;
}


.Aling-content--flex-start .FlexElemento,
.Aling-content--flex-end .FlexElemento,
.Aling-content--center .FlexElemento,
.Aling-content--stretch .FlexElemento,
.Aling-content--space-between .FlexElemento,
.Aling-content--space-around .FlexElemento{
    width: 40%;
}
.Aling-content--flex-start,
.Aling-content--flex-end,
.Aling-content--center,
.Aling-content--stretch,
.Aling-content--space-between,
.Aling-content--space-around{
    flex-wrap: wrap;
}
.Aling-content--flex-start{
    align-content: flex-start;
}
.Aling-content--center{
    align-content: center;
}
.Aling-content--flex-end{
    align-content: flex-end;
}
.Aling-content--stretch > .FlexElemento{
    height: initial;    
}
.Aling-content--stretch{
    align-content: stretch;
}
.Aling-content--space-around{
    align-content: space-around;
}
.Aling-content--space-between{
    align-content: space-between;
}


